'use strict';

import { parse } from 'path';

export default require.context('./', true, /\.(?:jsx?)$/).keys().reduce((r, v, i, arr) => {
  const { dir } = parse(v);
  const { dir: baseDir, base } = parse(dir);
  if (baseDir === '.') r[base] = require(`./${base}/index`);
  return r;
}, {});



