"use strict";

import React, { PropTypes } from 'react';
import Modal from 'react-modal';
import Form from 'muicss/lib/react/form';
import Input from 'muicss/lib/react/input';
import Select from 'react-select';
import Button from 'muicss/lib/react/button';
import MdSave from 'react-icons/lib/md/save';
import MdEdit from 'react-icons/lib/md/edit';
import MdClear from 'react-icons/lib/md/clear';

class Save extends MdSave {
  /* jshint ignore:start */
  static childContextTypes = {
    reactIconBase: PropTypes.object
  };
  /* jshint ignore:end */
  getChildContext() {
    return {
      reactIconBase: { size: 36, style: { 'margin-top': '10px', padding: '12px' } }
    };
  }
};

class Edit extends MdEdit {
  /* jshint ignore:start */
  static childContextTypes = {
    reactIconBase: PropTypes.object
  };
  /* jshint ignore:end */
  getChildContext() {
    return {
      reactIconBase: { size: 36, style: { padding: '4px' } }
    };
  }
};

class Dismiss extends MdClear {
  static childContextTypes = {
    reactIconBase: PropTypes.object
  };
  getChildContext() {
    return {
      reactIconBase: { size: 36, style: { padding: '4px' } }
    };
  }
}

export default ({
  isOpen,
  setRPCURL,
  ethereumRPC,
  selectAccount,
  editingRPCURL,
  onRPCURLChange,
  editRPCURL,
  saveRPCURL,
  tmpRPCURL,
  tmpSelectedAccount,
  selectedAccount,
  closeEthereumPanel,
  dismissEthereumPanel,
  gas,
  multiplier,
  changeGas,
  changeMultiplier,
  accounts
}) => (
  <Modal style={ {
    content: {
      top: '30%',
      left: '20%',
      width: '80%',
      height: '70%',
      right: 'auto',
      bottom: 'auto',
      transform: 'translate(-10%, -25%)',
    },
    overlay: {
      zIndex: 100000
    }
  } } isOpen={ isOpen } closeTimeoutMS={ 80 } contentLabel="Select Ethereum">
    <span className="pull-left">
      <h1>Ethereum Options</h1>
    </span>
    <span className="pull-right pointer ethereum-modal-dismiss-container" onClick={ dismissEthereumPanel }>
      <Dismiss />
    </span>
    <Form onSubmit={ setRPCURL }>
      <div className="rpc-form-slide">
        { editingRPCURL && <Input className="rpc-input" label="RPC URL" floatingLabel={ true } onChange={ onRPCURLChange } value={ tmpRPCURL } /> || <Input className="rpc-input" label="RPC URL" floatingLabel={ true } onChange={ onRPCURLChange } value={ ethereumRPC } disabled /> }
        <div className="inline-block 48by48 pointer" onClick={ editingRPCURL && saveRPCURL || editRPCURL } >
          { !editingRPCURL && <Edit /> || <Save /> }
        </div>
      </div>
    </Form>
    { accounts.length && <div className="account-select-container"><span className="account-select-header">Select Account</span><Select name="selected-account" className="account-select" options={ accounts.map(v => ({ value: v, label: v })) } onChange={ selectAccount } value={ tmpSelectedAccount } /></div> || <span></span> }
    <Form onSubmit={ closeEthereumPanel }>
      <div className="rpc-form-slide">
        <Input className="rpc-input" label="Gas Limit" floatingLabel={ true } onChange={ changeGas } value={ gas } />
      </div>
      <div className="rpc-form-slide">
        <Input className="rpc-input" label="Gas Price Multiplier" floatingLabel={ true } onChange={ changeMultiplier } value={ multiplier } />
      </div>
    </Form>
    <Button onClick={ closeEthereumPanel } variant="raised">Submit</Button>
  </Modal>
);



