'use strict';

import { createSelector } from 'reselect';
import BigRational from 'big-rational';
import BigNumber from 'big-number';
import isNullOrUndefined from '../lib/is-null-or-undefined';
import { rationalPrecision } from '../fixtures';

const defaultToken = {
  symbol: 'N/A',
  name: 'N/A',
  unselected: true
};

/* jshint ignore:start */

export const computeSellOrders = createSelector([
  ({ tokens }) => tokens,
  ({ selectedMarket }) => selectedMarket,
  ({ orders }) => orders,
  ({ pendingTrades }) => pendingTrades,
  ({ pendingCancels }) => pendingCancels,
  ({ tradeForMarket }) => tradeForMarket
], (tokens, selectedMarket, orders, pendingTrades, pendingCancels, tradeForMarket) => {
  const selected = tokens.find((v) => {
    return v.symbol.toLowerCase() === selectedMarket.toLowerCase();
  }) || defaultToken;
  const tradeFor = tokens.find((v) => {
    return v.symbol.toLowerCase() === tradeForMarket.toLowerCase();
  }) || defaultToken;
  const {
    address: tradeForAddress,
    decimals: tradeForDecimals
  } = tradeFor;
  const {
    address: selectedAddress,
    decimals: selectedDecimals
  } = selected;
  const selectedFactor = BigNumber(10).pow(selectedDecimals || 0);
  const tradeForFactor = BigNumber(10).pow(tradeForDecimals || 0);
  const ordersFormat = orders
          .filter(v => !v.invalid)
          .filter(v => v.tokenGive === tradeForAddress && v.tokenGet === selectedAddress)
          .filter(v => !pendingCancels.find(u => v.hash === u.hash))
          .map(v => {
            const { hash } = v;
            const pendingTradesForOrder = pendingTrades.filter(u => u.hash === v.hash);
            const pendingAmountGet = pendingTradesForOrder.map(v => v.amount).reduce((r, v) => (r.add(BigNumber(v))), BigNumber(0)).toString();
            const pendingAmountGive = pendingTradesForOrder.map(v => v.amountGiveAdjusted).reduce((r, v) => (r.add(BigNumber(v))), BigNumber(0)).toString();
            return {
              ...v,
              pendingAmountGet,
              pendingAmountGive
            };
          })
          .filter(v => ((!v.amountGetRemaining || !BigNumber(v.amountGetRemaining).minus(v.pendingAmountGet).equals(0)) && !BigNumber(v.amountGet).minus(BigNumber(v.pendingAmountGet)).equals(0) && !BigNumber(v.amountGive).minus(BigNumber(v.pendingAmountGive)).equals(0) && (!v.amountGiveRemaining || !BigNumber(v.amountGiveRemaining).minus(BigNumber(v.pendingAmountGive)).equals(0))))
          .map(v => {
            let priceRational = !isNullOrUndefined(selectedDecimals) && !isNullOrUndefined(tradeForDecimals) && BigRational(BigNumber(v.amountGet)).divide(selectedFactor).divide(BigRational(BigNumber(v.amountGive)).divide(tradeForFactor)) || BigRational(0);
            let getRational = BigRational(BigNumber(v.amountGetRemaining || v.amountGet).minus(v.pendingAmountGet)).divide(selectedFactor);
            let giveRational = BigRational(BigNumber(v.amountGiveRemaining || v.amountGive).minus(v.pendingAmountGive)).divide(tradeForFactor);
            return {
              ...v,
              priceRational,
              price: priceRational.eq(0) && 'N/A' || priceRational.toDecimal(rationalPrecision),
              getRational,
              get: getRational.toDecimal(rationalPrecision),
              giveRational,
              give: giveRational.toDecimal(rationalPrecision)
            };
          })
          .sort((a, b) => !(a.price === 'N/A' || b.price === 'N/A') && (BigRational(b.price).lt(BigRational(a.price)) && 1 || BigRational(b.price).gt(BigRational(a.price)) && -1 || BigRational(a.price).eq(BigRational(b.price)) && 0 || 0))
          .map((() => {
            let sum = BigRational(0);
            return (v) => ({
              ...v,
              sum: (sum = sum.add(BigRational(v.get))).toDecimal()
            })
          })());
  return ordersFormat;
});

export const computeSellTotal = createSelector([
  computeSellOrders,
  ({ tokens }) => tokens,
  ({ tradeForMarket }) => tradeForMarket
], (orders, tokens, tradeForMarket) => {
  const tradeFor = tokens.find((v) => v.symbol.toLowerCase() === tradeForMarket.toLowerCase()) || defaultToken;
  const {
    decimals: tradeForDecimals
  } = tradeFor;
  const tradeForFactor = BigNumber(10).pow(tradeForDecimals || 0);
  return typeof tradeForDecimals === 'number' && BigRational(orders.reduce((r, v) => r.add(v.amountGiveRemaining || v.amountGive), BigNumber(0))).divide(tradeForFactor).toDecimal() || 'Loading';
});

/* jshint ignore:end */



