"use strict";

import { parse } from 'path';

export default require.context('./', true, /^(.*\.(js$))[^.]*$/igm)
  .keys()
  .map((v) => parse(v).name)
  .filter((v) => v !== 'index')
  .reduce((r, v) => {
    const reducer = require(`./${v}`);
    r[reducer.key] = reducer;
    return r;
  }, {});



