const back = (ary) => ary[ary.length - 1];

const RETRY_INTERVAL = 5000, //is the time between retry attempts when there is a connection refused error or invalid JSON RPC from web3, in case the user loses connection to his RPC and has to restart it
      TRADE_ERROR_OFFSET = 3; //how much to add to the error returned by the `testTrade` function in the contract to get the error code in our errors.js file, so it will work with errorFromCode without having to have separate logic for each type of error returned by that function

const { stringify, parse } = JSON;

const catchLoadErr = (err) => dispatch({ type: 'LOAD_ERROR', payload: err.message });

const catchPendingErr = (err) => {
  if (err.name === 'PendingError') dispatch({
    type: 'LOAD_ERROR',
    payload: err.message
  });
  else throw err;
};

const padLeft = (s, n, char) => {
  if (s.length < n)
    return Array(n - s.length + 1).join(char) + s;
  return s;
};

const getStats = () => Promise.all([
  getBlock('latest'),
  getGasPrice(),
  loadBalances()
]).then(([ block, gasPrice, balances ]) => {
  return {
    gasLimit: +block.gasLimit,
    gasPrice: String(gasPrice), 
    balances
  };
});

const getAndDispatchStats = () => getStats().then((payload) => {
  dispatch.mutexDispatch({
    type: 'LOAD_STATS',
    payload
  });
});

let globalCancel = () => {};

const dispatchRetryConnect = () => {
  const { cancel, timer } = timeout(RETRY_INTERVAL);
  globalCancel = cancel;
  return timer.then(() => {
    return bootstrap();
  }).catch(catchWeb3);
};

const catchWeb3 = (err) => {
  if (err.message.match('Invalid JSON')) {
    dispatch({
      type: 'WEB3_DISCONNECTED'
    });
    return dispatchRetryConnect();
  }
  throw err;
};



