const getGasPrice = () => new Promise((resolve, reject) => {
  getEth().getGasPrice((err, result) => {
    if (err) return reject(err);
    resolve(+result);
  });
});

const getGasLimit = () => new Promise((resolve, reject) => {
  getEth().getBlock('latest', (err, result) => {
    if (err) return reject(err);
    return resolve(+result.gasLimit);
  });
});

const call = (ct, fn, ...args) => {
  const from = getState().selectedAccount;
  args.push({ from });
  return new Promise((resolve, reject) => {
    args.push((err, result) => {
      if (err) return reject(err);
      resolve(result);
    });
    ct[fn].call(...args);
  });
};

/*
 * This is a complex function that submits a transaction to the txpool from the client,
 * returning two promises, `mined` and `submitted`.
 *
 * `defer` is used so I can pass the `resolve` function
 * of the `promise` object to the `txpoll` branch of the state tree, which is accessed every time the block number goes up
 * to check if there is a transaction receipt.
 *
 * Also, when the `submitted` promise is rejected, it calls the `reject` function of the `mined` promise.
 */
const sendTransaction = (ct, fn, ...args) => {
  const { multiplier, gasPrice, gas, txpoll, selectedAccount } = getState();
  if (txpoll.find(v => v.from === selectedAccount)) {
    const err = PendingError('Transaction is already pending from this address');
    const promise = Promise.reject(err);
    dispatch({
      type: 'LOAD_ERROR',
      payload: err.message
    });
    return {
      mined: promise,
      submitted: promise
    };
  }
  const submittedDeferred = defer(),
        minedDeferred = defer();
  const retval = {
    submitted: submittedDeferred.promise,
    mined: minedDeferred.promise
  };
  submittedDeferred.promise.catch((err) => {
    if (err.message.match(/Error:\s+User\s+denied\s+/)) {
      return setImmediate(() => dispatch({
        type: 'LOAD_ERROR',
        payload: 'Error: User denied transaction signature.'
      }));
    }
    throw err;
  });
  const handleErr = (err) => {
    submittedDeferred.reject(err);
    minedDeferred.reject(err);
  };
  Promise.all([
    getBlockNumber(),
    (multiplier && getGasPrice().then((price) => multiplier*price) || Promise.resolve(+gasPrice)),
    (gas === 'max' && getGasLimit() || Promise.resolve(+gas))
  ]).then((results) => {
    const [ number, price, limit ] = results;
    let from;
    const details = { gasPrice: price, gas: limit, from: (from = getState().selectedAccount) };
    if (typeof back(args) === 'object') {
      assign(details, back(args));
      args = args.slice(0, args.length - 1).concat(details);
    } else args.push(details);
    args.push((err, tx) => {
      if (err) return handleErr(err);
      dispatch({
        type: 'TRANSACTION_DISPATCHED',
        payload: {
          tx,
          resolve: minedDeferred.resolve,
          fn,
          from,
          number,
          time: now(),
          address: ct.address,
          args: args.slice(0, -2)
        }
      });
      openEthereumPanel();
      submittedDeferred.resolve(tx);
    });
    ct[fn].sendTransaction(...args);
  }).catch((err) => {
    submittedDeferred.reject(err);
    minedDeferred.reject(err);
    catchPendingErr(err);
  }).catch(catchWeb3);
  return retval;
};

const getTransactionReceipt = (tx) => new Promise((resolve, reject) => {
  getEth().getTransactionReceipt(tx, (err, result) => {
    if (err) return reject(err);
    resolve(result);
  });
});

/*
 * Although the client only lets you submit one transaction at a time per address,
 * it supports many transactions from many different addresses concurrently.
 *
 * The transactions to be watched for a `mined` event are stored in the `txpoll` branch of the state tree,
 * and when a new block gets mined, `pollAll` is called to see if it has to resolve any `mined` promises
 * from `sendTransaction`, and remove these transactions from `txpoll`.
 */
const pollAll = () => mapSeries(getState().txpoll, (v, next) => {
  let result;
  getTransactionReceipt(v.tx).then((_result) => {
    result = _result;
    return getBlockNumber();
  }).then((number) => {
    if (result && (result.logs && result.logs.find((u) => u.type === 'mined') || result.blockNumber <= number)) {
/* jshint ignore:start */
      dispatch({
        type: 'TRANSACTION_MINED',
        payload: {
          ...v,
          receipt: result
        }
      });
/* jshint ignore:end */
      openEthereumPanel();
      if (v.resolve) v.resolve(result);
    }
    next(null, result);
  }).catch(catchWeb3).catch((err) => {
    dispatch({
      type: 'LOAD_ERROR',
      payload: err.stack
    });
    next(err);
  });
});

let cancelPoll;

const dispatchPollAll = (interval = 0) => new Promise((resolve, reject) => {
  cancelPoll = subscribeToKeyOnce('lastBlock', () => {
    pollAll().then(() => {
      resolve();
    }).catch((err) => reject(err));
  });
}).then(() => {
  let promise = dispatchPollAll();
  maybeStopPolling();
  return promise;
});

/* RAY here is a magic number that is better declared somewhere else 
   It is actually unused now; now all txes will be polled for a receipt on each block being mined.
*/
const startPollingReceipts = () => pollAll().then(() => {
  dispatchPollAll(15000);
});

const maybeStopPolling = () => {
  if (!getState().txpoll.length) stopPollingReceipts();
};

const stopPollingReceipts = () => {
  if (cancelPoll) cancelPoll();
};

/*
 * This is the actual function that calculates the hash that gets signed during an order,
 * the signature is passed to the contract along with these parameters, and the hash is recalculated
 * in the Ethereum code
*/
const hash = (address, tokenGet, amountGet, tokenGive, amountGive, expires, nonce, user) => sha3(address + tokenGet.substr(2) + padLeft(fromDecimal(amountGet).substr(2), 64, '0') + tokenGive.substr(2) + padLeft(fromDecimal(amountGive).substr(2), 64, '0') + padLeft(fromDecimal(expires).substr(2), 64, '0') + padLeft(fromDecimal(nonce).substr(2), 64, '0') + user.substr(2), { encoding: 'hex' });

const isMetamaskArg = (web3) => Boolean(web3 && web3.currentProvider && web3.currentProvider && web3.currentProvider.constructor && web3.currentProvider.constructor.name === 'MetamaskInpageProvider');

const isMetamask = () => isMetamaskArg(web3);

const sign = (addr, hash) => new Promise((resolve, reject) => {
  const { ethereumRPC } = getState();
  getEth().sign(addr, isMetamask() && saltHash(hash) || hash, (err, result) => {
    if (err) return reject(err);
    resolve(ln({
      r: result.substr(0, 66),
      s: '0x' + result.substr(66, 64),
      v: +fromDecimal('0x' + result.substr(130)) + ((ethereumRPC !== 'metamask' && ethereumRPC === 'default') && 27 || 0)
    }));
  });
});

const cancel = (hash) => {
  const target = getState().orders.find(v => v.hash === hash);
  const {
    tokenGet,
    amountGet,
    tokenGive,
    amountGive,
    expires,
    nonce,
    user,
    v,
    r,
    s
  } = target;
  const retval = sendTransaction(getMarketContract(), 'cancelOrder', tokenGet, amountGet, tokenGive, amountGive, expires, nonce, v, r, s, user);
  retval.submitted.then(() => fetch({
    method: 'POST',
    url: '/api/reread',
    json: {}
  })); 
  return retval;
};

/**
 * Function that submits a new order to the server
 *
 * @param tokenGet address of the token to get
 * @param amountGet amount of the token to get
 * @param tokenGive address of the token to give
 * @param amountGive amount of the token to give
 * @param expires amount of blocks before order ex
 * @param nonce unique number of the order
 */
const order = ({
  tokenGet,
  amountGet,
  tokenGive,
  amountGive,
  expires,
  nonce
}) => {
  const { selectedAccount: user, address } = getState();
  let hashed;
  return sign(user, (hashed = hash(address, tokenGet, amountGet, tokenGive, amountGive, expires, nonce, user))).then((sig) => {
    const { v, r, s } = sig;
    return fetch({
      method: 'POST',
      url: '/api/order',
      json: {
        tokenGet,
        amountGet,
        tokenGive,
        amountGive,
        expires,
        nonce,
        user,
        hash: hashed,
        v,
        r,
        s
      }
    });
  });
};

/**
 * Function that submits a trade to the Ethereum Contract and posts to reread afterwards.
 *
 * @param tokenGet address of the token to get
 * @param amountGet amount of the token to get
 * @param tokenGive address of the token to give
 * @param amountGive amount of the token to give
 * @param expires amount of blocks before order ex
 * @param nonce unique number of the order
 */
const trade = ({
  tokenGet,
  amountGet,
  tokenGive,
  amountGive,
  expires,
  nonce,
  user,
  v,
  r,
  s,
  amount
}) => {
  const retval = sendTransaction(getMarketContract(), 'trade', tokenGet, amountGet, tokenGive, amountGive, expires, nonce, user, v, r, s, amount);
  const { mined, submitted } = retval;
  submitted.then(() => {
    return fetch({
      method: 'POST',
      json: {},
      url: '/api/reread'
    });
  });
  mined.catch(catchWeb3);
  return retval;
};

const getBlockNumber = () => new Promise((resolve, reject) => {
  getEth().getBlockNumber((err, result) => {
    if (err) return reject(err);
    resolve(result);
  });
});

/**
 * Workhorse method that gets the current order from the state
 * and checks if it can be matched agains a targetOrder.
 *
 * If so, it will result in a trade.
 * Orderwise it will place a new order.
 *
 * @param type 'buy' or 'sell'
 * @param amount unused
 * @returns {*}
 */
const performTradeOrPlaceOrder = (type, amount) => {
  const {
    orders,
    selectedMarket,
    tradeForMarket,
    buyPrice,
    buyPriceRational,
    buyAmount,
    buyTotal,
    sellPrice,
    sellPriceRational,
    tokens,
    sellAmount,
    sellTotal,
    buyExpiry,
    sellExpiry,
    nonce
  } = getState();
  const decimals = tokens.map(v => v.decimals);
  const { address: tradeForAddress } = tokens.find(v => v.symbol === tradeForMarket);
  const { address: selectedAddress } = tokens.find(v => v.symbol === selectedMarket);
  const ordersFiltered = orders
    .filter(v => !v.invalid) //only valid orders
    .filter(v => (
      (BigRational(v.amountGet).neq(0) &&  //only orders with unzero wanted
       BigRational(v.amountGive).neq(0)) && //only orders with unzero offered
      (!v.amountGetRemaining || BigRational(v.amountGetRemaining).neq(0)) && //only orders with unzero amountGetRemaining or no amountGetRemaining
      (type === 'buy' && v.tokenGive === tradeForAddress && v.tokenGet === selectedAddress || type === 'sell' && v.tokenGet === tradeForAddress && v.tokenGive === selectedAddress))); //select only matching pairs
  const tradeForDecimals = decimals[tokens.findIndex(v => v.symbol === tradeForMarket)] || 0;
  const selectedDecimals = decimals[tokens.findIndex(v => v.symbol === selectedMarket)] || 0;
  const amt = type === 'buy' && BigRational(buyTotal).multiply(pow(10, selectedDecimals)).toDecimal() || type === 'sell' && BigRational(sellAmount).multiply(pow(10, tradeForDecimals)).toDecimal(); //determine amount
  let idx;
  const target = ordersFiltered.find((v, i) => {
    idx = i;
    const price = BigRational(type === 'buy' && v.amountGet || v.amountGive).divide(pow(10, selectedDecimals)).divide(BigRational(type === 'buy' && v.amountGive || v.amountGet).divide(pow(10, tradeForDecimals)));
    const priceIsEqual = price.eq(BigRational(type === 'buy' && (buyPriceRational || buyPrice) || type === 'sell' && (sellPriceRational || sellPrice)));
    const amountRemains = BigRational(v.amountGetRemaining || v.amountGet);
    const amountRemainsIsGeqAmt = amountRemains.geq(BigRational(amt));
    return priceIsEqual && amountRemainsIsGeqAmt;
  });
  if (target !== null && target !== undefined) {
    return call(getMarketContract(), 'testTrade', target.tokenGet, target.amountGet, target.tokenGive, target.amountGive, target.expires, target.nonce, target.user, target.v, target.r, target.s, ln(amt), getState().selectedAccount).then((result) => {
      result = +result;
      if (result) throw errorFromCode(result + TRADE_ERROR_OFFSET);
      const { mined, submitted } = trade({
        tokenGet: target.tokenGet,
        amountGet: target.amountGet,
        tokenGive: target.tokenGive,
        amountGive: target.amountGive,
        nonce: target.nonce,
        expires: target.expires,
        amount: amt,
        v: target.v,
        r: target.r,
        s: target.s,
        user: target.user
      });
      submitted.then(() => dispatch({
        type: type === 'sell' && 'CLEAR_SELL' || 'CLEAR_BUY'
      }));
      return mined;
    }).catch((err) => {
      dispatch({
        type: 'LOAD_ERROR',
        payload: err.message
      });
    });
  } else {
    let error;
    const totalErr = 'Please enter a valid total.',
          amountErr = 'Please enter a valid amount.';
    if (type === 'buy') {
      if (BigNumber(buyTotal).equals(0)) {
        dispatch({
          type: 'LOAD_ERROR',
          payload: totalErr
        });
        error = true;
      } else if (BigNumber(buyAmount).equals(0)) {
        dispatch({
          type: 'LOAD_ERROR',
          payload: amountErr
        });
        error = true;
      }
    } else {
      if (BigNumber(sellTotal).equals(0)) {
        dispatch({
          type: 'LOAD_ERROR',
          payload: totalErr
        });
        error = true;
      } else if (BigNumber(sellAmount).equals(0)) {
        dispatch({
          type: 'LOAD_ERROR',
          payload: amountErr
        });
        error = true;
      }
    }
    if (error) return Promise.reject('Order/trade failed.');
    dispatch({
      type: 'INCREMENT_NONCE'
    });
    return getBlockNumber().then((number) => {
      if (type === 'buy') {
        /*
         * RAY what is the comma expression in below find functions?
         * idx simply stores the index of the token in the tokens array, this comes from older code where `decimals` was its own branch of the state tree, but now I store the `decimals` value in the database instead of relying on the `decimals()` call of an ERC20 token. I could just as easily store the tokenGet object an access the `decimals` and `address` properties of that, but I use `decimals[idx]` which returns the precision of that particular token
         */
        const tokenGet = tokens.find((v, i) => ((idx = i), v.symbol === tradeForMarket)).address,
              amountGet = BigRational(buyAmount).multiply(pow(10, decimals[idx])).floor().toDecimal(),
              tokenGive = tokens.find((v, i) => ((idx = i), v.symbol === selectedMarket)).address,
              amountGive = BigRational(buyTotal).multiply(pow(10, decimals[idx])).floor().toDecimal(),
              expires = number + Number(buyExpiry);
        dispatch({
          type: 'ORDER_DISPATCHED'
        });
        return order({
          tokenGet,
          amountGet,
          tokenGive,
          amountGive,
          expires,
          nonce
        }).then((resp) => {
          if (resp.body.error) throw resp.body;
          dispatch({
            type: 'ORDER_SUBMITTED',
            payload: resp.body
          });
          dispatch({
            type: 'CLEAR_BUY'
          });
        }).catch((err) => {
          dispatch({
            type: 'LOAD_ERROR',
            payload: err.message
          });
        });
      } else {
        const tokenGive = tokens.find((v, i) => ((idx = i), v.symbol === tradeForMarket)).address,
              amountGive = BigRational(sellAmount).multiply(pow(10, decimals[idx])).floor().toDecimal(),
              tokenGet = tokens.find((v, i) => ((idx = i), v.symbol === selectedMarket)).address,
              amountGet = BigRational(sellTotal).multiply(pow(10, decimals[idx])).floor().toDecimal(),
              expires = number + Number(sellExpiry);
        dispatch({
          type: 'ORDER_DISPATCHED'
        });
        return order({
          tokenGet,
          amountGet,
          tokenGive,
          amountGive,
          expires,
          nonce
        }).then((resp) => {
          if (resp.body.error) throw resp.body;
          dispatch({
            type: 'ORDER_SUBMITTED',
            payload: resp.body
          });
          dispatch({
            type: 'CLEAR_SELL'
          });
        }).catch((err) => {
          dispatch({
            type: 'LOAD_ERROR',
            payload: err.message
          });
        });
      }
    }).catch((err) => {
      dispatch({
        type: 'LOAD_ERROR',
        payload: err.message
      });
    });
  }
};

const performBuyOrPlaceOrder = () => performTradeOrPlaceOrder('buy');
const performSellOrPlaceOrder = () => performTradeOrPlaceOrder('sell');

const deposit = () => {
  if (getState().tradeDepositAmount) {
    return tradeDeposit();
  } else {
    return baseDeposit();
  }
};

const baseDeposit = () => {
  let idx;
  const { address, decimals } = getState().tokens.find((v, i) => ((idx = i), getState().selectedMarket === v.symbol));
  const { baseDepositAmount, exchangeBalances, balances } = getState();
  const amtRational = BigRational(getState().baseDepositAmount);
  if (amtRational.leq(0)) return dispatch({
    type: 'LOAD_ERROR',
    payload: 'Please enter a valid deposit amount'
  });
  const amt = BigRational(baseDepositAmount).multiply(pow(10, decimals)).toDecimal();
  if (~possibleEthAddresses.indexOf(address)) {
    const { mined, submitted } = sendTransaction(getMarketContract(), 'deposit', ethAddress, amt, { value: amt });
    return mined;
  }
  const { mined, submitted } = sendTransaction(getTokenContract(address), 'approve', getState().address, amt);
  return mined.then(() => {
    const { mined } = sendTransaction(getMarketContract(), 'deposit', address, amt);
    return mined;
  }); 
};

const tradeDeposit = () => {
  let idx;
  const { address, decimals } = getState().tokens.find((v, i) => ((idx = i), getState().tradeForMarket === v.symbol));
  const amtRational = BigRational(getState().tradeDepositAmount);
  if (amtRational.leq(0)) return dispatch({
    type: 'LOAD_ERROR',
    payload: 'Please enter a valid deposit amount'
  });
  const amt = BigRational(getState().tradeDepositAmount).multiply(pow(10, decimals)).toDecimal();
  const { exchangeBalances, balances } = getState();
  if (~possibleEthAddresses.indexOf(address)) {
    const { mined, submitted } = sendTransaction(getMarketContract(), 'deposit', ethAddress, amt, { value: amt });
    return mined;
  }
  const { mined, submitted } = sendTransaction(getTokenContract(address), 'approve', getState().address, amt);
  return mined.then(() => {
    const { mined } = sendTransaction(getMarketContract(), 'deposit', address, amt);
    return mined;
  }); 
};

const getBlock = (n) => new Promise((resolve, reject) => {
  getEth().getBlock(n, (err, block) => {
    if (err) return reject(err);
    resolve(block);
  });
});

const withdraw = () => {
  const { baseWithdrawQuantity, tradeWithdrawQuantity, selectedMarket, tradeForMarket, tokens } = getState();
  let amt = baseWithdrawQuantity || tradeWithdrawQuantity;
  let token;
  let idx;
  if (baseWithdrawQuantity) token = tokens.find((v, i) => ((idx = i), v.symbol === selectedMarket));
  else token = tokens.find((v, i) => ((idx = i), v.symbol === tradeForMarket));
  amt = BigRational(amt).multiply(pow(10, token.decimals)).toDecimal();
  const { mined, submitted } = sendTransaction(getMarketContract(), 'withdraw', token.address, amt);
  submitted.then(() => dispatch({ type: 'CLEAR_WITHDRAW' })).catch(catchLoadErr);
  return mined;
};

const whiteList = ['0x', ''];

const bootstrap = () => {
  return setupWeb3().then(() => {
    const { selectedAccount, address } = getState();
    dispatch({
      type: 'RETRY_CONNECT_RPC'
    });
    return !getState().web3Error && selectedAccount && address && loadBalances() || Promise.resolve(true);
  });
};

if (getState().ethereumRPC) bootstrap();

const defaultRPC = 'http://localhost:8545';

const tryDefaultRPC = () => new Promise((resolve, reject) => {
  const web3 = new Web3(new Web3.providers.HttpProvider('http://localhost:8545'));
  web3.eth.getSyncing((err, result) => {
    if (err) {
      if (err.message.match('Invalid JSON')) return resolve(false);
      return reject(err);
    }
    resolve(true);
  });
});

subscribeToKey('txpoll', (lastTxpoll, currentTxpoll) => {
  if (!lastTxpoll.length && currentTxpoll.length) startPollingReceipts();
});

/* RAY explain the conditional logic in natural language here 
 * If the ethereumRPC branch of the state tree is not set, the application should check for the presence of metamask or an Ethereum-aware browser, via window.web3, if that fails it will try http://localhost:8545 once to see if it is a working RPC, if it succeeds then DEFAULT_RPC_SUCCESS will be fired and ethereumRPC will be set to http://localhost:8545  */
subscribeToState((lastTree, currentTree) => {
  if (currentTree.ethereumRPC === '' && !currentTree.tryingDefaultRPC && !currentTree.triedDefaultRPC) {
    if (externalWeb3 && false) dispatch({
      type: 'LOAD_ETHEREUM_RPC',
      payload: 'http://' + window.location.hostname + ':8545'
    });
    else if ((window.web3 || {}).eth) {
      if (window.chrome && isMetamaskArg(window.web3)) dispatch({
        type: 'LOAD_ETHEREUM_RPC',
        payload: 'metamask'
      });
      else dispatch({
        type: 'LOAD_ETHEREUM_RPC',
        payload: 'default'
      });
    } else {
      dispatch({
        type: 'TRYING_DEFAULT_RPC'
      });
      currentTree = getState();
      tryDefaultRPC().then((res) => {
        if (res) return dispatch({
          type: 'DEFAULT_RPC_SUCCESS',
          payload: defaultRPC
        });
        dispatch({
          type: 'DEFAULT_RPC_FAILED'
        });
      }).catch((err) => {
        dispatch({
          type: 'DEFAULT_RPC_FAILED',
          payload: err.stack
        });
      });
    }
  }
});

subscribeToKey('ethereumRPC', (lastRPC, newRPC) => {
  setupWeb3().then(() => loadBalances()).catch(catchWeb3);
});

subscribeToKey('selectedAccount', (lastAccount, newAccount) => {
  loadBalances();
  const xhr = new XMLHttpRequest();
  xhr.open('POST', prefix + '/api/sync');
  xhr.setRequestHeader('Content-Type', 'application/json;charset=UTF-8');
  xhr.onreadystatechange = () => {
    if (xhr.readyState === 4) {
      const payload = JSON.parse(xhr.responseText);
      dispatch({
        type: 'DO_SYNC',
        payload
      });
    }
  };
  xhr.send(JSON.stringify({ address: newAccount }));
}); 

subscribeToKey('lastBlock', (() => {
  let syncing = false;
  return () => {
    if (syncing) return;
    syncing = true;
    return Promise.all([
      getAndDispatchStats(),
      loadBalances()
    ]).then(() => {
      syncing = false;
      return true;
    }).catch((err) => {
      syncing = false;
      throw err;
    }).catch(catchWeb3);
  };
})());

(function dispatchGetAccounts() {
  if (getState().ethereumRPC) getAndDispatchAccounts();
  setTimeout(dispatchGetAccounts, getAccountsTimeout);
})();

export default {
  performBuyOrPlaceOrder,
  performSellOrPlaceOrder,
  withdraw,
  deposit,
  cancel
};
  



