'use strict';

import max from '../../lib/max';

export default {
  key: 'highestOrderId',
  defaultValue: 0,
  inject: {
    loader: true,
    reloadable: true
  },
  reducer: (state, action) => {
    switch (action.type) {
      case 'POLL_UPDATE':
        if ((action.payload.orders || []).length) return max(action.payload.orders.map(v => v.id));
    }
    return state;
  } 
};



